import React, { Component } from 'react';
import { Table } from 'semantic-ui-react';

export default class RequestRow extends Component {
	render() {
    const { Row, Cell } = Table;
    const { id, request } = this.props

		return (
			<Row>
				<Cell>{ id }</Cell>
				<Cell>{ request.description }</Cell>
				<Cell> </Cell>
				<Cell> </Cell>
				<Cell> </Cell>
				<Cell> </Cell>
				<Cell> </Cell>
			</Row>
		);
	}
}
